const initialState = {
    favouriteItems: [],
}

const favouritesReducer = (state = initialState, action) => {


    switch (action.type) {

        case 'SET_FAVOURITE_PRDOUCT':
            return { ...state, favouriteItems: [...state.favouriteItems, action.payload] }
        case 'REMOVE_FAVOURITE_PRODUCT':
            return { ...state, favouriteItems: state.favouriteItems = action.payload }
        default:
            return state
    }
}

export default favouritesReducer