const initialState = {
    isAuth: false,
    id: null,
    email: null,
    token: null,
    displayName: null,
    photoURL: null
}

const userReducer = (state = initialState, action) => {


    switch (action.type) {

        case 'SET_USER':
            return {
                ...state,
                isAuth: true,
                id: state.id = action.payload.id,
                email: state.email = action.payload.email,
                token: state.token = action.payload.token,
                displayName: state.displayName = action.payload.displayName,
                photoURL: state.photoURL = action.payload.photoURL === null ? '/avatar.png' :action.payload.photoURL,
            }
        case 'REMOVE_USER':
            return {
                ...state,
                isAuth: false,
                id: null,
                email: null,
                token: null,
                displayName: null,
                photoURL: null
            }
        default:

            return state
    }
}

export default userReducer