import React from 'react'
import { useDispatch} from 'react-redux'
import { REMOVE_USER } from '../../store/actions'

const LogOut = ({ logOut }) => {
    const dispatch = useDispatch() 
    
    const logOutUser = () => {
        dispatch(REMOVE_USER())
        logOut()
    }
    return (
        <div onClick={logOutUser}>
            log out
        </div>
    )
}

export default LogOut
