import React from 'react'
import { getAuth, createUserWithEmailAndPassword, updateProfile } from 'firebase/auth'
import { getAnalytics, setUserProperties } from "firebase/analytics";
import { useDispatch } from 'react-redux'
import { SET_USER } from '../../store/actions'
import { useNavigate } from 'react-router-dom'
import { useForm } from "react-hook-form";

const SignUp = () => {
    const dispatch = useDispatch()
    const navigate = useNavigate()

    const { register,
        handleSubmit,
        formState: { errors }
    } = useForm({
        mode: 'onBlur'
    });



    const registrationUser = (data) => {
        const auth = getAuth();
        const email = data.email
        const password = data.password
        const username = data.username
        createUserWithEmailAndPassword(auth, email, password)
            .then((userCredential) => {
                updateProfile(auth.currentUser, {
                    displayName: username, photoURL: null
                })
                dispatch(SET_USER({
                    email: userCredential.user.email,
                    id: userCredential.user.uid,
                    token: userCredential.user.accessToken,
                    displayName: username,
                    photoURL: null
                }))
                navigate(-1)
            })
            .catch((error) => {
                console.log(error)
            });
    }


    return (
        <form onSubmit={handleSubmit(registrationUser)} className='signIn'>
            <div className="title">
                Регистрация
            </div>
            <label>
                Электронная почта
                <input  {...register('email', {
                    required: 'Поле обязательно к заполнению',
                    pattern: {
                        value: /^([\w\.\-]+)@([\w\-]+)((\.(\w){2,3})+)$/,
                        message: "Почтовый адрес введен некорректно"
                    }
                })} />
                {errors?.email && <div className='formError'>{errors?.email?.message}</div>}
            </label>
            <label>
                Имя пользователя
                <input {...register('username', {
                    required: 'Поле обязательно к заполнению',
                    minLength: {
                        value: 6,
                        message: 'Длина имени более 6 символов'
                    }
                })} />
                {errors?.username && <div className='formError'>{errors?.username?.message}</div>}
            </label>
            <label>
                Пароль
                <input type='password' {...register('password', {
                    required: 'Поле обязательно к заполнению',
                    minLength: {
                        value: 8,
                        message: 'Длина пароля более 8 символов'
                    }
                })} />
                {errors?.password && <div className='formError'>{errors?.password?.message}</div>}
            </label>
            <input type="submit" value='Зарегистрироваться' />
        </form>
    )
}

export default SignUp
