import React from 'react'
import ReactStarsRating from 'react-awesome-stars-rating';
import { useSelector } from 'react-redux'
import ImageLoop from './ImageLoop';


const Comment = () => {
    const comentaries = useSelector(state => state.comments.comments)
    const [loopIsActive, setLoopIsActive] = React.useState(false)
    const [activeImage, setActiveImage] = React.useState('')

    const handleClick = (e) => {
        setLoopIsActive(true)
        setActiveImage(e.target.src)
        document.body.style.overflow = 'hidden'
    }

    return (
        <div>
            {loopIsActive &&
                <ImageLoop
                    comentaries={comentaries}
                    setLoopIsActive={setLoopIsActive}
                    activeImage={activeImage}
                />
            }
            {
                comentaries.map((item, i) => (
                    <div key={i} className="comment">
                        <div className="comment__user">
                            <div className="comment__user-avatar">
                                <img src={item.userAvatar} alt="avatar" />
                            </div>
                            <div className="comment__user-name">
                                {item.userName}
                            </div>
                            <div className="comment__user-data">
                                {item.date}
                            </div>
                        </div>
                        <div className="comment__raiting">
                            <div className="comment__raiting-tag">
                                Общий:
                                <ReactStarsRating
                                    isEdit={false}
                                    value={item.raiting}
                                    size={20}
                                    primaryColor={'#1875f0'} />
                            </div>
                        </div>
                        {
                            item.comment.map((commm, i) => (
                                <div key={i} className="comment__dignity">
                                    <div className="comment-title">
                                        {commm.name}
                                    </div>
                                    {commm.value}
                                </div>
                            ))
                        }
                        {item.files &&
                            <div className="comment__dignity">
                                <div className="comment-title">
                                    Фотографии
                                </div>
                                <div className="comment__images">
                                    {
                                        item.files.map((image, i) => (
                                            <div onClick={(e) => handleClick(e)} key={i} className="comment__images-item">
                                                <img src={image} alt="img" />
                                            </div>
                                        ))
                                    }
                                </div>
                            </div>
                        }
                    </div>

                ))
            }
        </div>
    )
}

export default Comment
